.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.top{
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: aqua;
}
.left{
  width:45%;
}
.right{
  width: 45%;
}
.menu{
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}
.nav{
  padding:25px 15px;
  font-size :20px;
  border-radius: 100px;
  cursor: pointer;
}
.nav:hover{
  background-color: brown;
}
/*1st section ends*/

/*2nd section starts*/
.container{
  width:100%;
  display: flex;
  background-color:black ;
  justify-content: flex-start;
  background-size: cover;
  text-align: center;
}
.box{
  margin: 20px;
  display: inline-block;
  width: 100px;
}
.imges{
  width: 620%;
  height:350px;
}
.box:nth-child(1){
  margin-left: 25px;
}
.row2{
  width: 100%;
  height: auto;
  display: flex;
  background-color: black;
}
.box1{
  width: 20%;
  margin-left: 95px;
  text-align: center;
}
.h1{
  margin-left: 10px;
  border-right:1pt solid red;
  font-size: 35pt;
  color: white;
}
.para{
  margin: 40px 20px;
  color: white;
}
span{
  cursor:pointer;
}
/* 2nd section ends */

/* 3rd section starts*/
.section3{
  width: 100%;
    background-color:rgb(38, 36, 36);
  display: flex;
  justify-content: center;
  column-gap: 2%;
  padding-bottom:20px;
  color: white;

}
.sub1, .sub2 , .sub3,.sub4{
  width:auto;
  background-color:rgb(38, 36, 36);

}
.btn1{
  margin: 5px;
  cursor: pointer;
  font-size: 10pt;
  padding: 5px;
  background-color: rgb(217, 77, 77);
  }
/*3rd section ends */

/* 4th section starts */
.fourth{
  width: 100%;
  background-color:black; 
  display: flex;
  justify-content: center;
  padding-bottom: 4%;
}
.f1{
  width:45%;
  display: flex;
}
.firstbox{
  width: 50%;
  color: white;
}
.para1{
  width:42%;
  padding-top: 12%;
  padding-right: 8%;
}
.mainmenu{
  display: flex;
  cursor: pointer;
  color: #e85356;
}

.class4{
  list-style:circle;
  padding: 5px 20px 20px 5px;
}

.secondbox{
  width: 45%;
}
.photoshoots{
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
.pic{
  width: 135px;
  height: 100px;
}

/*4th section ends*/

/*5th section  starts  */
.fifth{
  width:100%;
  display: flex;
  justify-content: center;
  background-color: red;

}
.copyrights{
  width: 45%;
}
.web{
  width: 50%;
  display: flex;
  justify-content: flex-end;
}
.pic2{
  width: 50px;
  height: 40px;
  padding: 10px;

}
/*section ends successfully*/
